import request from '../../util/request';
import common from '../../util/common';

export default {
    name: 'Classify',
    data () {
        return {
            result: [],
            firstEnter: true
        };
    },
    onLoad () {
        this.queryClassifyCount();
    },
    onTabItemTap () {
        if (!this.firstEnter) {
            this.queryClassifyCount();
        }
    },
    methods: {
        convertTotal (arg0) {
            let result = arg0;
            (result || 0).toString().replace(/(\d)(?=(?:\d{3})+$)/g, '$1,');
            result = '共' + result + '部';
            return result;
        },
        convertName(sourceName) {
            let result = '';
            if (sourceName === '笔趣阁') {
                result = 'biquge'
            } else if (sourceName === '147小说') {
                result = '147xiaoshuo'
            } else if (sourceName === '天天书吧') {
                result = 'tiantianshuba'
            } else if (sourceName === '飞库小说') {
                result = 'feikuxiaoshuo'
            } else if (sourceName === '趣书吧') {
                result = 'qushuba'
            }
            return result;
        },
        queryClassifyCount () {
            request.get('/novels/classifyCount', {}).then(data => {
                if (data.status === 200 && data.total > 0) {
                    this.result = data.data;
                }
            }).finally(() => {
                if (this.firstEnter) {
                    this.firstEnter = false;
                }
            });
        },
        searchBtn (sourceName) {
            uni.navigateTo({
                url: '/pages/result/SearchResult?params=' + JSON.stringify({ type: 'classify', sourceName: sourceName })
            });
        }
    }
};
